@import url(../fonds/clash-display.css);
*{
    font-family: ClashDisplay, sans-serif;
    font-weight: 500 !important;
}

.containerBig{
    background-color: rgba(255, 255, 255, 0.68);
    border-radius: 20px;
}

:root {
    --c-dark: #17252A;
    --c-light: #2B7A78;
    --c-brand-light: #3AAFA9;
    --c-brand: #ffcb00;
    --c-brand2: #ffdd3c;
    --c-body: #a3a3b6;
    --c-transparentMain: rgb(222, 242, 241);
    --c-colorMian: #FEFFFF;
    --box-shadow: 0px 15px 25px rgba(0, 0, 0, 0.08);
    --transition: all 0.5s ease;
}


.backgroundContainer {
    z-index: -1;
}

html {
    font-size: 1rem;
}

@media (min-width: 768px) {
    html {
        font-size: 1vw;
    }
}

@media only screen and (max-width: 768px) {
    HTML, body {
        width: 100%;
        overflow-x: hidden;
    }}

html, body {
    width: 100%;
    overflow-x: hidden;}